import React, {useEffect, useState, useRef} from "react";

const setNumber = (num, length) =>{

    return String(num).padStart(length, '0')
}

const Timer = (limit) =>{

    const [hour, setHour] = useState(setNumber(0,2));
    const [minute, setMinute] = useState(setNumber(0,2));
    const [second, setSecond] = useState(setNumber(0,2));

    const initialTime = useRef( 0 );
    const interval = useRef(null);

    useEffect(() => {

        interval.current = setInterval( ()=>{

            initialTime.current+=1;
            setSecond(setNumber(initialTime.current%60 , 2));
            setMinute(setNumber( parseInt(initialTime.current /60),2 ));
            setHour(setNumber( parseInt(initialTime.current /60 /60), 2 ));

        },1000 );

        return () => clearInterval(interval.current)
    },[] );

    

    useEffect( () =>{
        if( limit && initialTime.current >= limit) clearInterval(interval.current)
    } ,[second])
    
    
    
    return (
        
            <p><span>{hour}</span>:<span>{minute}</span>:<span>{second}</span></p>
        
    )

}

export default Timer;