
import React from 'react';
import {Route, Routes} from 'react-router-dom';
import MainLayout from './layout/Main';
import routerData from './router';


function App() {

  const MainLayoutPage = routerData.filter( router_ => router_.layout === "MainLayout");
  const ClassrommPage = routerData.filter(router_ => router_.layout=="ClassroomLayout");
  const Page = routerData.filter(router_ => router_.layout === null );

  return (

      <Routes>      
         <Route element = {<MainLayout />}>             
          {MainLayoutPage.map( (page, index) => {
            return <Route key= {"layout"+index} path = {page.path} element = {page.element} />
          } )}
        </Route>       
        {Page.map( (page, index) => {
            return <Route key= {"page"+index} path = {page.path} element = {page.element} />
          } )}      
      </Routes>      
    
  );
}

export default App;
