import {createSlice} from '@reduxjs/toolkit';


const metadata = [];
for(let i=0, j=10; i<j; i++){
    metadata[i] = {
        id:"Q"+(i),
        type : i<7? "obj" : "sub",
        correct: null,
        studentAnswer : [],
        button : i<7 ? [] : null,
        answer : [i],
        correct : false
    }
    if(metadata[i].type=="obj"){
        for(let m=0,n=5; m<n; m++){
            metadata[i].button.push({id:"Q"+(i)+"-"+m, check:false})
        }
    }
}

const studyStatus = [
    {id: "lecture1", name:"개념 강의 보기", pass:false},
    {id: "lecture2", name:"Self-Check", pass:false },
    {id: "lecture3", name:"개념 확인하기", pass:false},
    {id: "lecture4", name:"오답 클리닉", pass:false},
    {id: "lecture5", name:"개념 설명하기", pass:false}
]

const studyMiddleware = (storeApi) => {
    return (next) => {
      return (action) => {
        
        //api
  
        return next(action);
      };
    };
};

const lectureSlice = createSlice({

    name : "STUDY",
    initialState : {
        studyIndex : 0,
        activeIndex : 0,
        quizIndex: 0,
        metadata :metadata,
        status : [],
        mode : "study"
    },
    reducers : {
        
        next : (state,action) =>{
            
            state.quizIndex = action.payload
        },

        prev : (state,action) => {
            state.quizIndex = action.payload
        },

        move : (state, action) =>{
            state.quizIndex = action.payload
        },

        studentAnswer : (state, action) => {            
           
            state.metadata[state.quizIndex].studentAnswer = action.payload;            
           
        },

        studyPass : (state, action) => {
            state.status[state.studyIndex].pass = action.payload;
            if(state.studyIndex<studyStatus.length-1) {
                state.studyIndex +=1;
                state.activeIndex +=1;
            }    
        },

        studyMove : (state,action) =>{

            state.activeIndex = action.payload
        },

        correct : (state,action) => {
            
            state.metadata[action.payload[0]].correct = action.payload[1]
        }        
    }
})


export const {next, prev, move, studentAnswer, studyPass, correct, studyMove} = lectureSlice.actions;
export default lectureSlice.reducer;
