const PROCESS_ = (arg)=>{
console.log(arg)
    const STUDY = {
        "BOOK" : [
            "BASIC_standard", //개념서 기본
            "BASIC_final", //개념서 단원 마무리
            "BASIC_skillup", //개념서 skill up
            "TRANS_common",//유형서 공통
            "TRANS_skillup",//유형서 Skill up
            "RegulerEvaluation", //정기평가
            "ARCHE_E_standard", //아르케 초등 기본
            "ARCHE_E_final", //아르케 초등 단원 마무리
            "ARCEE_M" //아르케 중등
        ],
        "PLUS" : [
            "DESCRIPTION", //서술형 따라잡기
            "EX" //교과서 적중문제
        ]
    }

    const CURRICULUM_TITLE = {
        "001" : "개념 강의 보기",
        "002" : "Self-Check",
        "003" : "개념 확인하기",
        "004" : "오답 클리닉",
        "005" : "개념 설명하기",
        "006" : "단원 마무리",
        "007" : "Skill Up",
        "008" : "유형 학습",
        "009" : "맞춤 클리닉",
        "010" : "단원 평가",
        "011" : "총괄 평가",
        "012" : "기본기 다지기 문제풀이",
        "013" : "기본기 강화 문제풀이",
        "014" : "기본 유형 익히기",
        "015" : "서술형 따라잡기",
        "016" : "교과서 적중문제"
    }

    const CURRICULUM = {};
    CURRICULUM[STUDY.BOOK[0]] = ["001","002","003","004","005"];
    CURRICULUM[STUDY.BOOK[1]] = ["006","004"];
    CURRICULUM[STUDY.BOOK[2]] = ["007","004"];
    CURRICULUM[STUDY.BOOK[3]] = ["008","004","009"];
    CURRICULUM[STUDY.BOOK[4]] = ["007","004"];
    CURRICULUM[STUDY.BOOK[5]] = ["010","011"];
    CURRICULUM[STUDY.BOOK[6]] = ["001","005","012","004","013","004"];
    CURRICULUM[STUDY.BOOK[7]] = ["006","004"];
    CURRICULUM[STUDY.BOOK[8]] = ["001","005","014","004"];
    CURRICULUM[STUDY.PLUS[0]] = ["015"];
    CURRICULUM[STUDY.PLUS[1]] = ["016"];

    const CURRICULUM_ROUTER = {
        "001" : "Lecture",
        "002" : "SelfCheck",
        "003" : "Study",
        "004" : "WrongAnswer",
        "005" : "Record",
        "006" : "Study",
        "007" : "",
        "008" : "",
        "009" : "Clinic",
        "010" : "Evaluation",
        "011" : "Evaluation",
        "012" : "",
        "013" : "",
        "014" : "",
        "015" : "",
        "016" : ""
    }

    const studyName = STUDY[arg.type][arg.number];
    const curriculum = CURRICULUM[studyName];

    function getInfo(name){        
        const returnArray= []
        const data = name == "title"? CURRICULUM_TITLE : CURRICULUM_ROUTER;

        for(let i=0,j=curriculum.length; i<j; i++){
            returnArray.push(data[curriculum[i]])   
        }

        return returnArray;
    }


   return {
        process: studyName,
        curriculum : getInfo("title"),
        router :  getInfo("router")
    }
}

const PROCESS = (arg)=>{
    
    let STUDY = PROCESS_(arg)
    let statusArray = [];
    for(let i=0,j=STUDY.curriculum.length; i<j; i++){
        statusArray[i] = {id : "curriculum"+(i+1), title: STUDY.curriculum[i], pass:false }
    }

    return {
        process: STUDY.process,
        curriculum : STUDY.curriculum,
        router :  STUDY.router,
        status : statusArray
    }
}

export default PROCESS;

