    {grade : "E2", semester: 1, name : "노벰", period_s : "2022.05.01", period_f : "2023.02.28", bookcode: "E", peroid : false, "id":"book9"}
    , {grade : "E2", semester: 1, name : "뜨레스", period_s : "2022.03.01", period_f : "2023.02.28", bookcode: "E", peroid : false, "id":"book8"}
