import { Outlet, useLocation } from "react-router-dom";
import GnB from "./GnB";
import Lnb_M from "./Lnb-Main";
import Lnb_S from "./Lnb-Sub";
import {motion} from 'framer-motion';

const pageVariants = {
    initial: {
      opacity: 0
    },
    in: {
      opacity: 1
    },
    out: {
      opacity: 0
    }
};
  
const pageTransition = {
    type: "tween",
    ease: "linear",
    duration: 0.3
};

const MainLayout = () => {

    const { pathname } = useLocation();    

    return (
        <>
        <div className="float-left main-left" >            
            <div className="lnb"> 
                <Lnb_S />
                <Lnb_M />                
            </div> 
        </div>
        <div className="float-left main-right" >
            <GnB />           
            <motion.div key={pathname} initial="initial" animate="in" variants={pageVariants} transition={pageTransition} className = "contents">
                <Outlet></Outlet>
            </motion.div>
        </div>
        </>    
    )
}

export default MainLayout;