import React from 'react';
import { Link, useLocation } from 'react-router-dom';
import routerData from '../router';
import {useDispatch} from 'react-redux';
//import {setBookID} from '../redux/bookReducer';

const Lnb_M = () => {
      
    const lnbList = routerData.filter( router_ => Object.keys(router_.nav).indexOf("lnb_main") >-1);    
    const dispatch = useDispatch();
    const location = useLocation();   

    const makeBar = lnbList.map( (list,index) => {
       
        return <li key = {'lnb-m-'+index} className = {list.path == location.pathname? "active" : null} onClick = { 
                ()=> {
                   // dispatch(setBookID(null));                    
                }
            }>
                <Link to = {list.path}>{list.nav.lnb_main}</Link>
            </li>
    })
    return (
        <ul className="lnb-main">
            {makeBar}
        </ul>
    )
}

export default Lnb_M;