import React, { useEffect, useCallback } from "react";
import {useDispatch, useSelector} from 'react-redux';
import { bookInfo } from "../data";
import { Navigate,Link, useNavigate } from "react-router-dom";
import {changeBookID, setBookID, setLesson, setUnit} from '../redux/bookReducer';
import { initProcess } from "../redux/processReducer";
import PROCESS from '../process';

const BookStudy = () =>{

    
    const Book = useSelector( (state) =>state.BOOK); 
    const Curriculum = useSelector( (state)=>state.CURRICULUM);
    const MyBookData  = useSelector( (state) => state.BOOK.bookData);  
    const dispatch = useDispatch();
    const navigate = useNavigate();     

    const studyContentsList = Book.bookContents[2]; //임시데이터    

    let bookStudyList = [];
    for(let i=0,j=studyContentsList.length; i<j; i++){
        const lesson = studyContentsList[i];
        const key = Object.keys(lesson)[0]
        
        for(let m=0,n=lesson[key].length; m<n; m++){
            const array = [i,m,lesson[key][m]]
            if(!lesson[key][m].complete) bookStudyList.push(array)
        }       
    }

    const start = async(arg) =>{ //공통

        let STUDY =PROCESS({type:"BOOK",number:0});
        const setProcess = await(dispatch(initProcess(STUDY))).unwrap();
        console.log(setProcess)
        navigate("/"+STUDY.router[Curriculum.jindo],  { replace: false });
       
    }

    const studyList = bookStudyList.map( (bookList, index) => {
      
        const lessonList = bookInfo.list[MyBookData[2].semester-1]
        if(studyContentsList.length==0) return (<p>진행할 학습이 없습니다. 선생님께 문의하세요.</p>)
        return(
            <div key = {"study"+index} className="book-list">
                <img src='' alt='교재'/>
                <div className="book-info">
                    <div className="book-title">
                        <p>{bookInfo.name}</p>
                        <span>{lessonList[bookList[0]].unit[bookList[1]].page+"쪽"}</span>
                        <div>
                            <p>{lessonList[bookList[0]].lesson}</p>
                            <p>{bookList[2].title}</p>
                        </div>    
                    </div>
                    <div className="book-btn">
                        <button onClick = { ()=>{ dispatch(setBookID(2)) } }><Link to="/BookContentsList">학습목차</Link></button>
                        {/*<button><Link to="/Lecture">학습하기</Link></button>*/}
                        <button onClick = { ()=>{ start(["BOOK",0]) } }>학습하기</button>
                    </div>
                </div>
            </div>
        ) 
    })

    
    return(

        <div className="tabcon active" id="tab-1">
            {studyList}
        </div>





    )


}

export default BookStudy;