import React, {useState} from "react";
import BookStudy from "../tab/BookStudy";
import PlusStudy from "../tab/PlusStudy";

const MyClassRoom = () =>{
   
    const [bookStudy, changeTab] = useState(true);
    
    return (
       <>
            <h1 className ="contents-title">나의 강의실</h1>
            <ul className="contents-tab">
                <li onClick = {()=>{changeTab(true)}} className = {bookStudy?"active":null}><a>교재 학습</a></li>
                <li onClick = {()=>{changeTab(false)}} className = {bookStudy?null:"active"}><a>플러스 학습</a></li>
            </ul>
            <div className="contents-tabcon">
                {bookStudy && <BookStudy />}
                {!bookStudy && <PlusStudy />}
            </div>            
        </>
    )

    
}

export default MyClassRoom;