import {createAsyncThunk,createSlice, current} from '@reduxjs/toolkit';
import { getVideoJindo } from '../parsing/videoJindo';

const videoJindo = [...getVideoJindo];
function setVideoJindo(courseArray){

    let getIndex = 0;
    for(let i=0,j=courseArray.length; i<j; i++){
        if(courseArray[i].pass) getIndex = i
    }
   
    if(getIndex>0 && courseArray[getIndex].freepass) getIndex+=1;
    else if(getIndex%2==0 && courseArray[getIndex].pass) getIndex+=2;

    return getIndex;
}

const index= setVideoJindo(videoJindo);

const videoSlice = createSlice({

    name : "VIDEO",
    initialState : {
        jindo : index,
        activeVideo : 0,
        status : videoJindo        
    },
    reducers : {
        
        pass : (state,action) =>{
            
            state.status[state.activeVideo].pass = action.payload;           
            state.jindo = setVideoJindo(current(state).status);
            
        },

        move : (state, action) => {           

            state.status[state.activeVideo].pass = true;
            state.activeVideo = action.payload;
            state.jindo = setVideoJindo(current(state).status);
        },
        
        progress : (state, action) =>{

           if( state.status[state.activeVideo].progress < action.payload ) state.status[state.activeVideo].progress = action.payload;
        }
    },

    extraReducers : {
       
        
    }
})


export const {pass, move, progress} = videoSlice.actions;
export default videoSlice.reducer;
