import {createSlice,createAsyncThunk,current} from '@reduxjs/toolkit';


const initProcess = createAsyncThunk(

    'init',  (argumements) =>{          
        
        return true;       
    }
  )

  
const curriculumSlice = createSlice({

    name : "CURRICULUM",
    initialState : {
        process: null,
        curricullum : null,
        router : null,
        jindo : 4,
        activeStep : 0,       
        status : null       
    },
    reducers : {

        init : (state,action) =>{           
            
            state.process = action.payload.process;
            state.curricullum = action.payload.curriculum;
            state.router = action.payload.router;
            state.status = action.payload.status;
        },

        reset : (state) =>{

            state.process = null;
        },
        
        setJindo : (state, action) =>{

            state.jindo = action.payload;
        },

        setStep : (state,action) => {
            state.activeStep = action.payload
        },
        
        setStatus : (state,action) =>{

            state.status = action.payload
        }       
    },
    extraReducers : {
        [initProcess.pending] : (state,action) =>{           

            state.process = action.meta.arg.process;
            state.curricullum = action.meta.arg.curriculum;
            state.router = action.meta.arg.router;
            state.status = action.meta.arg.status;             
        },
        [initProcess.fulfilled] : (state,action) =>{
            
        },
        [initProcess.rejected] : (state,action) =>{}
    }
})

export {initProcess};
export const { init, reset, setJindo, setStep, setStatus } = curriculumSlice.actions;
export default curriculumSlice.reducer;
